import { Component, OnInit } from '@angular/core';
import { GaUniformService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzMessageService } from 'ng-zorro-antd';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    status: string = ''

    constructor(
        private gaUniformService: GaUniformService,
        private spinner: NgxSpinnerService,
        private activatedRoute: ActivatedRoute,
        private nzMessageService: NzMessageService,
    ) {}

    pageIndex = 1
    pageSize = 10
    total = 1
    listOfData: any[] = []
    loading = true
    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''

    ngOnInit() {
        this.activatedRoute.paramMap.subscribe(
            data=>{
                if(data.get('status')){
                    this.status = data.get('status')
                    this.searchData()
                }
            }
        )
    }
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.searchData()
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1
        }
        this.loading = true
        this.gaUniformService
            .getGaUniforms(
                JSON.stringify({
                    pageIndex: this.pageIndex,
                    pageSize: this.pageSize,
                    sortKey: this.sortKey,
                    sortValue: this.sortValue,
                    search: this.search,
                    status: this.status,
                }),
            )
            .subscribe((data: any) => {
                this.loading = false
                this.total = data.data.total
                this.listOfData = data.data.data
                console.log(data.data)
            })
    }

    updateFilter(value: string): void {
        this.search = value
        this.searchData(true)
    }

    delete(id) {
        this.spinner.show()
        this.gaUniformService.deleteGaUniformsId(id).subscribe(data => {
            this.searchData()
            this.spinner.hide()
        })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

}
