import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { UniformRoutingModule } from './uniform-routing.module';
import { EditComponent } from './edit/edit.component';
import { ListComponent } from './list/list.component';
import { AddComponent } from './add/add.component';
import { DetailComponent } from './detail/detail.component';
import { NgZorroAntdModule, NzLayoutModule, NzRadioModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { ApprovalModule } from 'src/app/components/approval/approval.module';
import { DocumentModule } from 'src/app/components/document/document.module';
import { UniformComponent } from './uniform.component';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { WidgetsComponentsModule } from 'src/app/components/widgets/widgets-components.module';
import { LayoutModule } from 'src/app/components/layout/layout.module';
import { PipeModule } from 'src/app/pipe/pipe.module';


@NgModule({
  declarations: [EditComponent, ListComponent, AddComponent, DetailComponent, UniformComponent],
  imports: [
    CommonModule,
    UniformRoutingModule,
    NgZorroAntdModule,
    ReactiveFormsModule,
    PerfectScrollbarModule,
    DocumentModule,
    WidgetsComponentsModule,
    NzLayoutModule,
    LayoutModule,
    FormsModule,
    NzRadioModule,
    ApprovalModule,
    PipeModule

],
providers:
[
    FormBuilderTypeSafe
],
})
export class UniformModule { }
